@mixin nc-year-and-month($height, $fontSize) {
  float: left;
  @include dimension($height);
  font-size: $fontSize;
  color: #666;
  font-weight: 200px;
  text-align: center;
}

@mixin position($position: relative) {
  position: relative;
  display: block;
  float: left;
}

@mixin dimension($height, $width: 100%) {
  width: $width;
  height: $height;
}

@mixin calendarDay($background: none, $color: #444) {
  .cdc-day {
    @include dimension(100%);
    font-size: 12px;
    font-weight: 300;
    color: #444;
    text-align: center;
    @include position;
    span {
      @include position;
      color: $color;
      background: $background;
      @include dimension(30px, 30px);
      margin-top: -15px;
      margin-left: -15px;
      left: 50%;
      top: 50%;
      border-radius: 100%;
      line-height: 30px;
      &:hover {
        cursor: pointer;
        background: #eee;
      }
    }
  }
}

@mixin input-focus-and-label($isFocused: false) {
  cursor: pointer;
  @if ($isFocused) {
    color: #3f51b5;
  }
  transform: translate(15px, 13px);
  &::after {
    content: "";
    @if ($isFocused) {
      border-bottom: 2px solid #fff !important;
    } @else {
      border-bottom: 1px solid #fff !important;
    }
    width: 100%;
    left: 0px;
    top: 7px;
    z-index: -2000;
    position: absolute;
  }
}

@mixin focus-and-has-value($isFocused: false) {
  outline: 0;
  border: 1px solid #ccc;
  @if ($isFocused) {
    border: 2px solid #3f51b5 !important;
  }
}
