  cursor: pointer;
  -webkit-transform: translate(15px, 13px);
          transform: translate(15px, 13px);
}

.input-label-container input:not(:placeholder-shown) + label::after {
  content: "";
  border-bottom: 1px solid #fff !important;
  width: 100%;
  left: 0px;
  top: 7px;
  z-index: -2000;
  position: absolute;
