* *::before,
*::after {
  box-sizing: border-box;
}

.error-message {
  color: #f44336;
  font-size: 0.75em;
}

.input-label-container {
  display: flex;
  flex-flow: column;
  position: relative;
  label {
    width: fit-content;
    padding: 0 5px;
    transition: all 0.2s;
    touch-action: manipulation;
    position: absolute;
    top: -20px;
    left: -5px;
    font-size: 13px;
    font-family: Arial, Helvetica, sans-serif;
  }
  input {
    @include dimension(38px);
    padding: 0px 14px;
    border-radius: 2.5px;
    border: 1px solid #aaa;
    font-size: 16px;
    &::-webkit-input-placeholder {
      opacity: 0;
      transition: inherit;
    }
    &:hover {
      border: 1px solid;
    }
    &:focus {
      @include focus-and-has-value(true);
    }
    &:not(:placeholder-shown) {
      @include focus-and-has-value;
    }
    &:placeholder-shown + label {
      cursor: text;
      width: fit-content;
      transform-origin: -25px 0px;
      color: #999999;
      transform: translate(0, 28px) scale(1.4);
    }
    &:focus::-webkit-input-placeholder {
      opacity: 1;
    }
    &:not(:placeholder-shown) + label {
      @include input-focus-and-label;
    }
    &:focus + label {
      @include input-focus-and-label(true);
    }
  }
}

.nc-container {
  float: left;
  position: absolute;
  z-index: 2;
  width: 360px;
  min-height: 350px;
  background: #fff;
  box-shadow: 10px 10px 40px rgba(0, 0, 0, 0.2);
  border-radius: 20px;
  overflow: hidden;
  padding: 25px 30px;

  .nc-head {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .nc-button {
      @include dimension(100%, 45px);
      position: relative;

      .nc-inner {
        float: left;
        @include dimension(35px, 35px);
        background: #f4f4f4;
        border-radius: 100%;
        text-align: center;
        position: absolute;
        margin-top: -17px;
        padding: 2px;
        display: flex;
        justify-content: center;
        align-items: center;

        &:hover {
          cursor: pointer;
          background: #eee;
          span {
            border-color: #555 !important;
          }
        }
      }
    }
    .ndpch-container {
      float: left;
      @include dimension(100%, 120px);

      .ndpchc-year {
        @include nc-year-and-month(30px, 27px);
      }
      .ndpchc-month {
        @include nc-year-and-month(15px, 13px);
      }
    }
  }

  .nc-body {
    float: left;
    width: 100%;
    margin-top: 20px;
    .nc-day-container {
      @include position;
      @include dimension(16.6666%, 14.285%);
      @include calendarDay;
    }
    .disabled {
      pointer-events: none;
      @include calendarDay(#fff !important, #ddd);
    }
    .highlight {
      @include calendarDay(#efdbca);
    }
    .highlight-green {
      @include calendarDay(#d4e2cb);
    }
    .inc-container {
      @include position;
      @include dimension(100%);
      .cc-head {
        @include dimension(30px);
        margin-top: 10px;
        @include position;
      }
      .cch-name {
        @include dimension(30px, 14.285%);
        line-height: 30px;
        font-weight: 700;
        color: #666;
        font-size: 12px;
        text-align: center;
        @include position;
      }
      .cc-body {
        @include dimension(270px);
        @include position;
      }
    }
  }
}
